// Add HTML tag as a function into array, so for an array like a = ['abc', 'def']
// a.TD = TD(a)
// a.mapTD = [TD('abc', TD('def'))]
// a.TDp(prop: dict) = TD(a, prop)
// a.mapTDp(prop: dict) = [TD('abc', prop), TD('def', prop)]
// where TD is h('td', ...) from hyperscript
function addTags(o) {
    if (o.hasOwnProperty('TD')) {
        return o;
    }
    var add = function (tag) {
        var r = {};
        r[tag] = { get: function () { return addTags(h(tag, this)); } };
        r[tag + 'p'] = {
            value: function (p) {
                return addTags(h(tag, p, this));
            }
        };
        r['map' + tag] = {
            get: function () {
                return addTags(this.map(function (e) {
                    return h(tag, e);
                }));
            }
        };
        r['map' + tag + 'p'] = {
            value: function (p) {
                return addTags(this.map(function (e) {
                    return h(tag, p, e);
                }));
            }
        };
        return r;
    };
    'TD TR TABLE H1 H2 H3 H4 H5 H6 DIV'.split(' ').forEach(function (tag) {
        return Object.defineProperties(o, add(tag));
    });
    return o;
}
